:root {
  --primary: black;
  --secondary: white;

  /*** BREAKING POINTS ***/
  --screen-sm: 576px;
  --screen-md: 768px;
  --screen-lg: 992px;
  --screen-xl: 1200px;
  --screen-xxl: 1400px;

  /*** font sizes ***/
  --font-desktop-sm: 0.8rem;
  --font-desktop-md: 1.3rem;
  --font-desktop-lg: 3rem;

  --font-mobile-sm: 1rem;
  --font-mobile-md: 1.5rem;
  --font-mobile-lg: 3.5rem;

  /*** flex ***/
  --flex-gap: 2.5rem;
}
