.nav {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-top: 2rem;
  gap: var(--flex-gap);
  border-bottom: 2px var(--primary) solid;
  padding-bottom: 1rem;
}

.linksContainer {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  gap: var(--flex-gap);
}
.linksContainer {
  position: relative;
}
.linkIcon {
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: -1;
  visibility: hidden;
}
.navLink {
  transition: 1s all;
  position: relative;
  left: 0px;
  border-bottom: 1px var(--primary) solid;
  font-size: var(--font-desktop-md);
  background-color: rgba(black, 0.3);
}
.navLink:hover {
  padding: 0.4rem;
  box-shadow: -10px 6px 0px -2px var(--primary);
  left: 10px;
}
.navLink:hover > .linkIcon {
  display: inline-block;
  left: -30px;
  top: 7px;
  visibility: visible;
  transition: 0.4s all;
}

@media all and (min-width: 768px) {
  .nav,
  .linksContainer {
    flex-direction: row;
  }
}
